import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>

class Bottom extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed!important',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
            title="店铺"
            key="home"
            icon={
                <i className='iconfont icon-dianpu'></i>                           
            }
            selectedIcon={
                <i className='iconfont icon-dianpu'></i>                           
            }
            selected={ pathname === '/' }
            onPress={ () => {
                this.props.history.push('/');
            } }
            data-seed="logId"
        >
        </TabBar.Item>
        <TabBar.Item
            icon={
                <i className='iconfont icon-jibenicon_shoucang'></i>                           
            }
            selectedIcon={
                <i className='iconfont icon-jibenicon_shoucang'></i>                           
            }
            title="收藏"
            key="cart"
            selected={ pathname === '/cart' }
            onPress={ (e) => {
                this.props.history.push('/cart');
                this.props.dispatch({
                    type:'ADD_TODO',
                    value:e.target.value
                })
            } }
            data-seed="logId1"
        ></TabBar.Item>
        <TabBar.Item
            icon={
                <i className='iconfont icon-xinbaniconshangchuan-'></i>
            }
            selectedIcon={
                <i className='iconfont icon-xinbaniconshangchuan-'></i>
            }
            title="客服"
            key="Friend"
            selected={ this.state.selectedTab === 'greenTab' }
            onPress={ () => {

            } }
        ></TabBar.Item>       
         <TabBar.Item
                icon={<button style={{height:'35px',width:'100px',borderTopLeftRadius:'20px',borderTopRightRadius:'0px',borderBottomLeftRadius:'20px',borderBottomRightRadius:'0px',backgroundColor:"orange",color:'white'}}>加入购物车</button> }
                key="madd to cart"
                onPress={ () => {
                    window.location='http://www.alipay.com'
                } }
            >
			</TabBar.Item>
            <TabBar.Item
                icon={<button style={{height:'35px',width:'100px',borderTopLeftRadius:'0px',borderTopRightRadius:'20px',borderBottomLeftRadius:'0px',borderBottomRightRadius:'20px',backgroundColor:"red",color:'white'}}>立即购买</button> }
                key="take it now"
                onPress={ () => {
                    window.location='http://www.alipay.com'
                } }
            >
            </TabBar.Item>

                </TabBar>
            </div>
        );
    }
}
export default withRouter(Bottom);











