import React from 'react'
import { SearchBar } from 'antd-mobile';

const Search = (props) => {
    return (
        <div>
            <div>
                <SearchBar placeholder="寻找宝贝店铺"></SearchBar>
                <button onClick={()=>props.history.push('/home')}>取消</button>
            </div>
        </div>
    )
}

export default Search
